import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators, FormControl} from '@angular/forms';
import {PageTitleService} from '../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {BaseComponent} from "../../core/component/base-component";
import {Params, ActivatedRoute} from "@angular/router";
import {Client} from "../../client/metier/client.metier";
import {ClientService} from "../../client/service/client.service";
import {CustomValidators} from "ng2-validation/dist/index";
import {BanqueService} from "../../admin/banque/service/banque.service";
import {Banque} from "../../admin/banque/metier/banque.metier";
import {Reglement} from "../../reglement/metier/reglement.metier";
import {ReglementService} from "../../reglement/service/reglement.service";

@Component({
  selector: 'ms-reglement-edit',
  templateUrl: './reglement.edit.component.html',
  styleUrls: ['./reglement.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class ReglementEditComponent extends BaseComponent implements OnInit {

  private reglement: Reglement;
  public reglementForm: FormGroup;

  constructor(private fb: FormBuilder,
              private route: ActivatedRoute,
              private pageTitleService: PageTitleService,
              private reglementService: ReglementService,) {
    super();
  }

  ngOnInit() {
    this.loadReglement();
    this.pageTitleService.setTitle("MODIFIER UN RÉGLEMENT");
    this.reglementForm = this.fb.group({

      numero: '',
      date: null,
      client: null, id: '',
      etatPaiement: '',
      typePaiement: '',
      montant: 0,
      facturation: null,
      banqueLibelle: null,
      numeroCheque: '',
    });
  }

  loadReglement() {
    this.route.params.switchMap((params: Params) => this.reglementService.getReglement(+params['id']))
      .subscribe(reglement => {
          this.reglement = reglement;
          this.reglementForm.patchValue(this.reglement);
          this.reglementForm.get("client").setValue(this.reglement.facturation.client.nom);
          this.reglementForm.get("etatPaiement").setValue(this.reglement.facturation.etatPaiement);
          if (this.reglement.typePaiement === 'CHEQUE') {
            this.reglementForm.get("banqueLibelle").setValue(this.reglement.banque.libelle);
          }
          // this.disableReglement();
        }, error => this.showError(error.status, error.json().message)
      );
  }


  annulerReglement(reglementForm: any) {
    if (!confirm("Etes-vous de vouloir annuler ces cette reglement !")) {
      return;
    }
    this.reglement = new Reglement(reglementForm);
    this.reglementService.annulerReglement(this.reglement)
      .subscribe(() => {
        this.showInfo("Annulation effectué avec succès");
        this.router.navigate(['/facturation/list']); // Redierction vers list de facturation
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  checkTypePaiement() {
    let typePaiement = this.reglementForm.get('typePaiement').value;
    if (typePaiement != '' && typePaiement != null) {
      if (typePaiement == 'ESPECE') {
        return true;
      }
      else {
        return false;
      }
    } else {
      return true;
    }
  }


  reglementEditRedirect() {

  }


}






