"use strict";

import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {fadeInAnimation} from "../../core/route-animation/route.animation";
import {Reglement} from "../metier/reglement.metier";
import {BaseComponent} from "../../core/component/base-component";
import {ReglementService} from "../service/reglement.service";
import {FormGroup} from "@angular/forms";
import {PageTitleService} from "../../core/page-title/page-title.service";
import {ActivatedRoute} from "@angular/router";
import {FacturationService} from "../../facturation/service/facturation.service";

@Component({
  selector: 'ms-reglement-list',
  templateUrl: './reglement.list.component.html',
  styleUrls: ['./reglement.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})

export class ReglementListComponent extends BaseComponent implements OnInit {

  public reglementForm: FormGroup;
  private reglement: Reglement;
  private reglementList: Array<Reglement> = [];

  constructor(private pageTitleService: PageTitleService,
              private reglementService: ReglementService,
              private facturationService: FacturationService,
              private route: ActivatedRoute) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES REGLEMENTS");
    this.loadReglementList();
  }

  private loadReglementList() {

    this.route.queryParams.subscribe(params => {
      let facturation_id: number = params['facturation_id'];
      this.facturationService.getFacturation(facturation_id)
        .subscribe(facturation => {
            this.reglementService.getReglementByFacturation(facturation).subscribe(reglementList => {
              this.reglementList = reglementList;

            }, error => {

              this.showError(error.status, error.json().message);
            });
          }, error => this.showError(error.status, error.json().message)
        );

    });
  }

}



