import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {ReglementRoutes} from './reglement.routing';
import {ReglementService} from "./service/reglement.service";
import {ReglementEditComponent} from "./reglement.edit/reglement.edit.component";
import {ReglementListComponent} from "./reglement.list/reglement.list.component";
import {FacturationService} from "../facturation/service/facturation.service";
import {DataTableModule} from "angular2-datatable";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(ReglementRoutes)
  ],
  declarations: [
    ReglementEditComponent,
    ReglementListComponent,
  ],
  providers: [
    ReglementService,
    FacturationService,
  ],
})

export class ReglementModule {
}



