import {Routes} from '@angular/router';

import {ReglementEditComponent} from "./reglement.edit/reglement.edit.component";
import {ReglementListComponent} from "./reglement.list/reglement.list.component";

export const ReglementRoutes: Routes = [{
  path: '',
  redirectTo: 'list',
  pathMatch: 'full',
}, {
  path: '',
  children: [
    {path: 'list', component: ReglementListComponent},
    {path: 'edit/:id', component: ReglementEditComponent},
  ]
}];



